import React from 'react';
import { useDispatch, useSelector } from "react-redux";
import { Button, Input, Row } from "reactstrap";
import { getPostList, addPost, loadMore } from "../../../redux/actions/postAction";
import { Post } from '../../../components/Community';
import Loading from '../../../components/Loading';
import './Community.scss';
import avatar from '../../../assets/img/theme/default-avatar.png'
import { useState } from 'react';
import { useEffect } from 'react';

function Community() {
    const dispatch = useDispatch()
    const [content, setContent] = useState('')
    const [files, setFiles] = useState([])
    const [limit] = useState(5)
    const [offset, setOffset] = useState(0)
    const postData = useSelector(state => state.post.data)
    const loading = useSelector(state => state.post.loading)

    useEffect(() => {
        dispatch(getPostList({ limit, offset }))
    }, [])

    const handleUploadFile = (e) => {
        let tempArr = [];
        if (e.target.files.length) {
            Object.keys(e.target.files).forEach(i => {
                tempArr.push(e.target.files[i]);
            })
            setFiles(tempArr)
        }
    }

    const handleAddPost = e => {
        e.preventDefault();
        if (content) {
            dispatch(addPost(content, files))
            setContent('')
            setFiles([])
        }
    }

    const handleLoadMore = () => {
        const tempOffset = offset + limit
        setOffset(tempOffset)
        dispatch(loadMore({ limit, offset }))
    }

    return (
        <>
            {loading && <Loading />}
            <div className="lg-3 container community">
                <div className="mt-3">
                    <img alt="..." className="avatar avatar-lg rounded-circle md-hidden" src={avatar} />
                </div>
                <div className="mt-3 ml-3 w-100">
                    <form onSubmit={handleAddPost}>
                        <textarea
                            className="form-control card-body"
                            placeholder="Type a message here ..."
                            onChange={(e) => setContent(e.target.value)}
                            name="content"
                            value={content}
                        />
                        <div className="community__form my-3 mb-3">
                            <div>
                                <Input
                                    accept="file/*"
                                    id="text-button-file"
                                    className="d-none"
                                    type="file"
                                    multiple
                                    onChange={handleUploadFile}
                                />
                                <div className="d-flex align-items-center align-self-center mt-2">
                                    <label htmlFor="text-button-file">
                                        <i className="ni ni-cloud-upload-96 community__form__icon" />
                                    </label>
                                </div>
                            </div>
                            <Button type="submit" className="community__form__button border-0" disabled={loading}>
                                Post
                                </Button>
                        </div>
                        <div className="text-right community__list-files">
                            {
                                files.length > 0 && <label><ul>
                                    {
                                        files.map((item, i) => {
                                            return <li key={i}>{item.name}</li>
                                        })
                                    }
                                </ul></label>
                            }
                        </div>
                    </form>
                </div>
            </div>
            {
                postData.result && postData.result.map((data, index) => {
                    return (
                        <div className="post-list container" key={index}>
                            <Post editable={true} post={data} />
                        </div>
                    )
                })
            }
            {
                postData.result && postData.result.length >= postData.count ? "" :
                    <Row className="d-flex justify-content-center mb-3">
                        <Button onClick={handleLoadMore} color="link"> Load More </Button>
                    </Row>
            }
        </>
    );
}

export default Community;