import React from "react";
import { useDispatch } from "react-redux";
import { useState } from "react";
import {
  Button,
  Card,
  CardHeader,
  Input,
  ButtonDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  InputGroup,
  Row,
} from "reactstrap";
import {
  getDateTime,
  stripHtml,
  getUserFullName,
} from "../../helpers/UtilsHelper";
import { getFileNameFromPath } from "../../helpers/FileHelper";
import {
  addComment,
  updatePost,
  deletePost,
} from "../../redux/actions/postAction";
import ListComment from "./ListComment";
import "./Post.scss";
import avatar from "../../assets/img/theme/default-avatar.png";

function ListFile(props) {
  const files = props.listFiles;
  return files.map((file, i) => {
    const fullName = getFileNameFromPath(file.postFile);
    return (
      <div key={i} className="d-flex justify-content-start">
        <a
          className="post-wrapper__body__file-name"
          href={file.postFile}
          target="_blank"
          rel="noopener noreferrer"
        >
          <li>{fullName}</li>
        </a>
      </div>
    );
  });
}

function Post(props) {
  const dispatch = useDispatch();

  const { post, editable } = props;
  const postId = post.id;

  const [comment, setComment] = useState("");
  const [content, setContent] = useState("");
  const [dropdownOpen, setDropdownOpen] = useState(false);
  const [modal, setModal] = useState(false);
  const toggleAction = () => {
    setDropdownOpen(!dropdownOpen);
  };
  const toggleUpdate = () => {
    setModal(!modal);
  };

  const isDisable = !stripHtml(content);
  const userName = post.createdBy && getUserFullName(post.createdBy);

  const handleAddComment = (e) => {
    e.preventDefault();
    if (comment) {
      dispatch(addComment(postId, comment));
      setComment("");
    }
  };

  const handleUpdatePost = (e) => {
    e.preventDefault();
    if (content !== post.content) {
      dispatch(updatePost(postId, content));
      toggleUpdate();
    }
  };

  const handleDeletePost = (e) => {
    e.preventDefault();
    dispatch(deletePost(postId));
  };

  return (
    <Card className="post-wrapper">
      <CardHeader className="post-wrapper__header">
        <Row className="d-flex justify-content-space-around align-items-start">
          <div>
            <img
              alt="..."
              className="avatar rounded-circle"
              src={
                post.createdBy && post.createdBy.avatar
                  ? post.createdBy.avatar
                  : avatar
              }
            />
          </div>
          <div className="post-wrapper__header__info">
            <a
              className="text-dark text-sm info__user-name"
              href="#"
              onClick={(e) => e.preventDefault()}
            >
              {userName}
            </a>
            <small className="d-block">
              {" "}
              {post.updatedAt && getDateTime(post.updatedAt)}{" "}
            </small>
          </div>
          <div className={editable ? "post-wrapper__header__btn" : "d-none"}>
            <ButtonDropdown isOpen={dropdownOpen} toggle={toggleAction}>
              <DropdownToggle color="link" className="dropdown-customs">
                <i className="ni ni-settings-gear-65" />
              </DropdownToggle>
              <DropdownMenu>
                <DropdownItem onClick={toggleUpdate}>Update</DropdownItem>
                <DropdownItem divider />
                <DropdownItem onClick={handleDeletePost}>Delete</DropdownItem>
              </DropdownMenu>
            </ButtonDropdown>
          </div>
        </Row>
      </CardHeader>
      <div>
        <p className="post-wrapper__body__content">{post.content}</p>
        <div
          className={
            `${post.files}` && `${post.files.length}` > 0
              ? "post-wrapper__body__file-list"
              : "d-none"
          }
        >
          <ListFile listFiles={post.files} />
        </div>
        {post.comments &&
          post.comments.map((rep, i) => {
            return (
              <div key={i}>
                <ListComment editable={editable} commentData={rep} />
              </div>
            );
          })}
        <form onSubmit={handleAddComment}>
          <InputGroup className="mb-4 ml-3">
            <Input
              placeholder="Write your comment"
              rows="1"
              type="input"
              onChange={(e) => setComment(e.target.value)}
              name="comment"
              value={comment}
              style={{ color: "black" }}
            />
            <Button type="submit" color="link">
              <i className="ni ni-send text-info" />
            </Button>
          </InputGroup>
        </form>
      </div>
      <Modal isOpen={modal} toggle={toggleUpdate}>
        <form onSubmit={handleUpdatePost}>
          <ModalHeader toggle={toggleUpdate}>Update This Post</ModalHeader>
          <ModalBody>
            <Input
              type="textarea"
              defaultValue={post.content}
              onChange={(e) => setContent(e.target.value)}
              name="content"
              autoFocus={true}
            />
            <div className="mt-3">
              <ListFile listFiles={post.files} />
            </div>
          </ModalBody>
          <ModalFooter>
            <Button color="primary" disabled={isDisable} type="submit">
              Save
            </Button>{" "}
            <Button color="link" onClick={toggleUpdate}>
              Cancel
            </Button>
          </ModalFooter>
        </form>
      </Modal>
    </Card>
  );
}

export default Post;
