import React from "react";
import { useDispatch } from "react-redux";
import {
  Button,
  Input,
  ButtonDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
} from "reactstrap";
import {
  getDateTime,
  stripHtml,
  getUserFullName,
} from "../../helpers/UtilsHelper";
import { updateComment, deleteComment } from "../../redux/actions/postAction";
import avatar from "../../assets/img/theme/default-avatar.png";
import { useState } from "react";

export default function ListComment(props) {
  const dispatch = useDispatch();
  const { commentData, editable } = props;
  const commentId = commentData.id;
  const postId = commentData.post;
  const [comment, setComment] = useState("");
  const [dropdownOpen, setDropdownOpen] = useState(false);
  const toggleAction = () => {
    setDropdownOpen(!dropdownOpen);
  };
  const [modal, setModal] = useState(false);
  const toggleUpdate = () => {
    setModal(!modal);
  };
  const isDisable = !stripHtml(comment);
  const userName =
    commentData.createdBy && getUserFullName(commentData.createdBy);

  const handleUpdateComment = (e) => {
    if (comment !== commentData.comment) {
      dispatch(updateComment(commentId, postId, comment));
      toggleUpdate();
    }
  };

  const handleDeleteComment = (e) => {
    dispatch(deleteComment(commentId, postId));
  };

  return (
    <div className="comment-wrapper">
      <div className="comment-wrapper__header">
        <div>
          <img
            alt={`Avatar from ${userName}`}
            className="avatar rounded-circle"
            src={
              commentData.createdBy && commentData.createdBy.avatar
                ? commentData.createdBy.avatar
                : avatar
            }
          />
        </div>
        <div className=" ml-3">
          <a
            className="text-dark text-sm"
            href="#"
            onClick={(e) => e.preventDefault()}
          >
            {userName}
          </a>
          <small className="d-block">
            {commentData.updatedAt && getDateTime(commentData.updatedAt)}
          </small>
        </div>
        <div className={editable ? "comment-wrapper__header__btn" : "d-none"}>
          <ButtonDropdown isOpen={dropdownOpen} toggle={toggleAction}>
            <DropdownToggle size="sm" color="link" className="dropdown-customs">
              <i className="ni ni-settings-gear-65" />
            </DropdownToggle>
            <DropdownMenu>
              <DropdownItem onClick={toggleUpdate}>Update</DropdownItem>
              <DropdownItem divider />
              <DropdownItem onClick={handleDeleteComment}>Delete</DropdownItem>
            </DropdownMenu>
          </ButtonDropdown>
        </div>
      </div>
      <div className="ml-6 mr-2 my-2">
        <p
          className="comment-wrapper__comment "
          style={{ fontSize: "17px", fontWeight: "500" }}
        >
          {commentData.comment}
        </p>
      </div>
      <Modal isOpen={modal} toggle={toggleUpdate}>
        <form onSubmit={handleUpdateComment}>
          <ModalHeader toggle={toggleUpdate}>Update This Comment</ModalHeader>
          <ModalBody>
            <Input
              type="textarea"
              defaultValue={commentData.comment}
              onChange={(e) => setComment(e.target.value)}
              autoFocus={true}
            />
          </ModalBody>
          <ModalFooter>
            <Button color="primary" disabled={isDisable} type="submit">
              Save
            </Button>{" "}
            <Button color="link" onClick={toggleUpdate}>
              CANCEL
            </Button>
          </ModalFooter>
        </form>
      </Modal>
    </div>
  );
}
